// Relative element should be position: relative;
@media(max-width: 767px) {
  .decoration-element-wizard {
    display: none;
  }

  .decoration-element-mobile {
    z-index: 1;
    display: block !important;
    position: absolute;
    top: 50%;
    left: -7px;
    transform: translate(0, -50%);
    width: 12px;
    height: 95%;
    border-radius: 20px;
    background-color: #585077;
  }

  .decoration-element-circle-arrow {
    display: none;
  }
}
